.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f4f4;
}

.card {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 90%;
  box-sizing: border-box;
  text-align: center;
}

.input-section {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}

.input-field {
  padding: 10px;
  width: 80%;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.button {
  padding: 10px 20px;
  background-color: #004c98;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  white-space: nowrap;
}

/* 비활성화 버튼 */
.button:disabled {
  background-color: #ccc;
}

.parked-cars h3 {
  margin-bottom: 10px;
}

ul {
  list-style: none;
  padding: 0;
}

.car-item {
  margin: 10px 0;
  font-weight: bold;
}

/* 출차버튼 */
.exit-button {
  background-color: #0877e6;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}

.exit-button:hover {
  background-color: #2d5c8a;
}